.contact-section {
  background-color: #f4f4f4;
  color: #333;
  padding: 5rem 2rem;
  text-align: center;
  min-height: 100vh;
}

.contact-title {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 2rem;
  font-weight: 700;
}

.contact-form {
  max-width: 600px;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.contact-form input,
.contact-form textarea {
  padding: 1rem;
  border-radius: 0.5rem;
  border: none;
  background: #1a1a1a;
  color: #fff;
  font-size: 1rem;
}

.contact-form textarea {
  height: 150px;
  resize: none;
}

.contact-form button {
  background: #000;
  /* color: #000; */
  color: #f4f4f4;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.contact-form button:hover {
  background: #00cccc;
}

.success-message {
  color: #00ff00;
  font-size: 1.2rem;
  margin-top: 1rem;
}  